	//高德SDK
	import amap from '@/common/SDK/amap/amap-wx.js';
	import uniRate from "@/components/uni-rate/uni-rate.vue"
	import {  
	    mapState,  
	    mapMutations  
	} from 'vuex'; 
	export default {
		data() {
			return {
				city:'广州',
				bigId: 1,
				bannerImg: [],
				tabIndex: 0,
				tabClasslist: [],
				storelist: [],
				page: 1,
				loadingType: 0,
				contentText: {
					contentdown: "上拉显示更多",
					contentrefresh: "正在加载...",
					contentnomore: "没有更多数据了"
				},
				mapinfo:[],
				cateid:0
			}
		},	
		components: {uniRate},
        computed: mapState(['Location']), 
		onLoad(e) {
			if(e.id!=''&&e.id>0){
				this.cateid=e.id;
				this.tabIndex=e.id;
			}
			var _this =this;
			_this.tabClasslist = [{sc_name: '首页',sc_id: '-1'},{sc_name: '全部',sc_id: '0'}];
			_this.$Request.post('index').then(res => {
				if (res.code == 200) {
					console.log(res);
					_this.pgList = res.datas.pglist;
					_this.bannerImg = res.datas.aplist;
					if(res.datas.sc_list){
						for (let i = 0; i < res.datas.sc_list.length;i++) {
							_this.tabClasslist.push(res.datas.sc_list[i]);
						}
					}
					console.log(_this.tabClasslist);
				}
			});
			console.log(this.Location);
			if(JSON.stringify(this.Location) !== "{}") {　　　　　　　　
				console.log('==not null===');
				
				this.mapinfo =this.Location;
				this.city = this.Location.city_name;
				if( this.Location.has_agent==1){
					this.has_agent=true;
				}
				this.loadData();
			}else{
				this.locationMap();
			}
			//this.locationMap();
			//this.loadData();
		},
		methods: {
			async loadData() {
				var _this =this;
				
				_this.$Request.post('shop/shop_list',{cateid:_this.cateid,page:10,curpage:_this.page,lat:_this.mapinfo.lat,lng:_this.mapinfo.lng,adcode:_this.mapinfo.adcode,city_id:_this.mapinfo.city_id}).then(res => {
					if (res.code == 200) {
						console.log(res);
						_this.storelist = res.datas.store_list;
						/* this.bannerImg = res.datas.aplist;
						if(res.datas.sc_list){
							for (let i = 0; i < res.datas.sc_list.length;i++) {
								this.tabClasslist.push(res.datas.sc_list[i]);
							}
						} */
						console.log(_this.tabClasslist);
					}
				});
				
			},
			locationMap(){
				var _this =this;
				_this.amapPlugin = new amap.AMapWX({
					//高德地图KEY，参考：http://ask.dcloud.net.cn/article/35070
					key: '57796d182a0849a4bff2b46fd6088ed3'
				});
				//定位地址
				_this.amapPlugin.getRegeo({
					success: data => {
						_this.mapinfo={lat:data[0].latitude,lng:data[0].longitude,adcode:data[0].regeocodeData.addressComponent.adcode};
						console.log('-----amap---------');
						console.log(data);
						_this.city = data[0].regeocodeData.addressComponent.city.replace(/市/g, ''); //把"市"去掉
						this.loadData();
					}
				});
			},
			swiperChange(e) {
				this.bigId = e.detail.current
			},
			toSearch:function(){
				uni.navigateTo({
					url:"/pages/search/search"
				})
			},
			toCity:function(){
				console.log('tocity');
				uni.navigateTo({
					url:"/pages/index/city?url=cate",
				})
			},
			toStore: function(store_id) {
				uni.navigateTo({
					//url:"/pages/member/login",
					url:"/pages/store/index?store_id="+store_id,
				})
			},
			toCategory(url){
				uni.navigateTo({
					url:url
				})
			},
			toPage(id){
				uni.navigateTo({
					url:'/pages/member/points/pinfo?id='+id
				})
			},
			tapTab(cateid){
				this.cateid=cateid;
				this.tabIndex=cateid;
				if(cateid==-1){
					uni.switchTab({
						url:"index"
					})
				}else{
					this.loadData();
				}
			}
		}
	}
